<template>
    <el-drawer
        :model-value="modelValue"
        :title="title"
        @close="onCancel"
        class="m-talent-recommend__drawer m-rank-pop"
        append-to-body
    >
        <el-form :model="form" label-width="80px" label-position="left">
            <el-form-item label="">
                <el-input :model-value="subtype" disabled></el-input>
            </el-form-item>
            <el-form-item label="" prop="key">
                <el-select
                    v-model="active"
                    filterable
                    allow-create
                    placeholder=""
                    style="width: 100%"
                    @change="onKeyChange"
                    default-first-option
                >
                    <el-option
                        v-for="item in computedData"
                        :key="item.key"
                        :label="item.key"
                        :value="item.id"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="client">
                <el-select v-model="form.client" placeholder="" style="width: 100%">
                    <el-option
                        v-for="item in clients"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="type">
                <el-select v-model="form.type" placeholder="" style="width: 100%">
                    <el-option
                        v-for="item in types"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="subtype">
                <el-select v-model="form.subtype" placeholder="" style="width: 100%">
                    <el-option
                        v-for="item in subtypes"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item :label="typeLabel" prop="code">
                <el-input v-model="form.code" :placeholder="typePlaceholder">
                    <template #prepend>
                        <a class="u-get" target="_blank" :href="typeHref">
                            <el-icon><Warning></Warning></el-icon>
                            
                        </a>
                    </template>
                </el-input>
            </el-form-item>
            <el-form-item label="" prop="pz_code">
                <el-input v-model="form.pz_code" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="desc">
                <el-input v-model="form.desc" type="textarea" placeholder="" :rows="4"></el-input>
            </el-form-item>
        </el-form>

        <template #footer>
            <div>
                <el-button type="danger" class="u-del-btn" @click="onDelete" :disabled="saveLoading"></el-button>
                <el-button @click="onCancel" :disabled="saveLoading"></el-button>
                <el-button type="primary" @click="onSave" :disabled="saveLoading"></el-button>
            </div>
        </template>
    </el-drawer>
</template>

<script>
import { markRaw } from "vue";
import { mapState } from "pinia";
import { useStore } from "@/store";
import { addTalentRecommend, putTalentRecommend, delTalentRecommend } from "@/service/talent_recommend";
import { omit, uniqBy } from "lodash";
export default {
    name: "TalentRecommendDialog",
    props: {
        modelValue: {
            type: Boolean,
            default: false,
        },
        data: {
            type: Array,
            default: () => [],
        },
        mount: {
            type: [String, Number],
            default: 0,
        },
    },
    emits: ["update:modelValue", "update"],
    data() {
        return {
            form: {
                type: "",
                subtype: "",
                key: "",
                client: "",
                code: "",
                pz_code: "",
                desc: "",
            },
            saveLoading: false,
            active: "",

            clients: markRaw([
                { label: "", value: "std" },
                { label: "", value: "origin" },
            ]),
            types: markRaw([
                { label: "", value: "talent" },
                { label: "", value: "talent2" },
            ]),
            subtypes: markRaw([
                { label: "pvp", value: "pvp" },
                { label: "pve", value: "pve" },
            ]),
        };
    },
    computed: {
        ...mapState(useStore, ["client"]),
        title() {
            return "";
        },
        typeLabel() {
            return this.form.type === "talent" ? "" : "";
        },
        typePlaceholder() {
            return this.form.type === "talent" ? "" : "";
        },
        typeHref() {
            return this.form.type === "talent" ? "/macro/talent" : "/macro/talent2";
        },
        subtype() {
            return this.$route.query?.subtype || "";
        },
        computedData() {
            return uniqBy(
                this.data.concat(
                    ["", "", "", "", "", ""].map((item) => {
                        return {
                            key: item,
                            id: item,
                        };
                    })
                ),
                "key"
            );
        },
    },
    methods: {
        init() {
            if (this.client === "std") {
                this.form.client = "std";
                this.form.type = "talent";
            } else {
                this.form.client = "origin";
                this.form.type = "talent2";
            }
        },
        onKeyChange() {
            const _data = this.data.find((item) => item.id == this.active);
            if (_data) {
                this.form = omit(_data, ["id"]);
            } else {
                this.form = {
                    type: "",
                    subtype: "",
                    key: "",
                    client: "",
                    code: "",
                    pz_code: "",
                    desc: "",
                };
            }
        },
        onCancel() {
            this.$emit("update:modelValue", false);
            // 
            this.form = {
                type: "",
                subtype: "",
                key: "",
                client: "",
                code: "",
                pz_code: "",
                desc: "",
            };
            this.active = "";
        },
        onSave() {
            const item = this.data.find((item) => item.id == this.active);
            if (item) {
                this.put();
            } else {
                this.create();
            }
        },
        create() {
            this.saveLoading = true;
            addTalentRecommend({ ...this.form, key: this.active, mount: this.mount })
                .then(() => {
                    this.$notify({
                        title: "",
                        message: "",
                        type: "success",
                    });
                    this.onCancel();
                    this.$emit("update");
                })
                .catch((err) => {
                    console.log(err);
                })
                .finally(() => {
                    this.saveLoading = false;
                });
        },
        put() {
            this.saveLoading = true;
            const data = omit(this.form, ["user_id", "updated_at", "created_at", "mount"]);
            putTalentRecommend(this.active, { ...data })
                .then(() => {
                    this.$notify({
                        title: "",
                        message: "",
                        type: "success",
                    });
                    this.onCancel();
                    this.$emit("update");
                })
                .catch((err) => {
                    console.log(err);
                })
                .finally(() => {
                    this.saveLoading = false;
                });
        },
        onDelete() {
            const item = this.data.find((item) => item.id == this.active);
            if (item) {
                this.$confirm(", ?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning",
                })
                    .then(() => {
                        this.saveLoading = true;
                        delTalentRecommend(this.active)
                            .then(() => {
                                this.$notify({
                                    title: "",
                                    message: "",
                                    type: "success",
                                });
                                this.onCancel();
                                this.$emit("update");
                            })
                            .catch((err) => {
                                console.log(err);
                            })
                            .finally(() => {
                                this.saveLoading = false;
                            });
                    })
                    .catch(() => {});
            } else {
                this.onCancel();
            }
        },
    },
};
</script>

<style lang="less">
@import "@/assets/css/rank.less";

.m-talent-recommend__drawer {
    .u-get {
        .el-icon {
            .pr;
            top: 2px;
        }
    }
}
</style>
